@font-face{font-family:'D-condensed';font-style:normal;font-weight:normal;src:url("../fonts/DINCondensed-Bold.woff") format("woff")}@font-face{font-family:'D-alternate';font-style:normal;font-weight:normal;src:url("../fonts/DINAlternate-Bold.woff") format("woff")}*{font-family:'D-condensed';transition:all 0.7s cubic-bezier(0.77, 0, 0.175, 1),background-position 0ms}html,body{height:100%;padding:0;margin:0;position:relative;width:100%;box-sizing:border-box;color:#000}h1{color:#fff}p,button{font-family:'D-alternate';line-height:1.8em;text-align:center;margin:inherit;color:#000}.App{opacity:0;transition:all 0.8s ease-out}.visible{visibility:visible}.overflowHidden{overflow:hidden}.title{font-family:'D-condensed';background:0;outline:0;color:#fff;white-space:nowrap;margin:0;padding:0;border:0;font-size:22.5em;position:absolute;top:50%;left:50%;transform:translate(-50%, -30%);transition:all 0.7s cubic-bezier(0.77, 0, 0.175, 1),background-position 0ms}.cursorPointer{cursor:pointer}.no-cursor{cursor:default}.pseudoBackground{width:100%;min-height:100vh;height:auto;background:url("../img/oops.jpg") no-repeat center center fixed;background-size:cover;-webkit-background-size:cover;-moz-background-size:cover;-o-background-size:cover;background-attachment:fixed}@media (max-width: 576px){.title{font-size:6em}}@media (min-width: 576px){.title{font-size:6em}}@media (min-width: 768px){.title{font-size:9.75em}}@media (min-width: 992px){.title{font-size:12.75em}}@media (min-width: 1200px){.title{font-size:13.5em}}
