.stills{position:relative;background-color:#C4E1B1;height:75vw;max-height:100vh;overflow:hidden}.stills button{height:1.5em;width:1.5em;border-radius:50%;font-size:3em;bottom:0.2em}.carouselProject_slide{background-color:#00000000;position:absolute;height:90%;width:90%;max-height:100vh;z-index:1;top:50%;left:50%;transform:translate(-50%, -50%)}.carouselProject_slide img{position:absolute;height:100%;max-height:80%;width:auto;top:48%;left:50%;transform:translate(-50%, -50%)}.carouselProject_history{position:absolute;z-index:2;bottom:0.4em;height:1.5em;width:50%;left:50%;transform:translateX(-50%)}.carouselProject_history ul{list-style:none;padding:0;margin:0;display:flex;justify-content:center;height:100%}.carouselProject_history ul li{height:100%;width:1em;box-sizing:border-box;position:relative}.carouselProject_history ul li:not(:last-of-type){margin-right:1em}.carouselProject_history ul li button{border:0;outline:0;position:absolute;background-color:rgba(255,255,255,0.5);width:0.35em;height:0.35em;cursor:pointer}.carouselProject_history ul li button.active{background-color:rgba(255,255,255,0.95)}.carouselProject_control{position:absolute;z-index:2;bottom:0;height:100%;display:block;background-color:#00000000;border:0;padding:0 5px;width:10%;overflow:hidden;cursor:pointer;outline:0}.carouselProject_control span{display:inline-block;width:2vw;height:2vw;max-width:0.3em;max-height:0.3em;position:relative;box-sizing:border-box;opacity:0.65;border-bottom:3px solid white;border-left:3px solid white}.carouselProject_control:hover span{opacity:1}.carouselProject_control__prev{left:0;padding-left:12px}.carouselProject_control__prev span{transform:rotate(45deg)}.carouselProject_control__next{right:0;padding-right:12px}.carouselProject_control__next span{transform:rotate(-135deg)}
