.Nav{position:absolute;top:0;z-index:2}.nameLink{font-size:1.5em;position:fixed;margin-left:2%;text-decoration:none;transition:all 0.4s ease-out;font:'D-condensed'}.nameLink:hover{color:#000}.aboutLink{font-size:1.5em;position:fixed;right:0;margin-right:2%;cursor:pointer;transition:all 0.4s ease-out;border:0;background:0;color:#fff;font-family:'D-condensed';margin-top:0.3em;text-align:right;padding:0;outline:0}.aboutLink:hover{color:#000}.XButton{position:absolute;cursor:pointer;border:0;background:0;outline:0;top:0.75em;left:50%;transform:translate(-50%, -50%);transition:all 0.4s ease-out;color:#fff;z-index:3;height:50px;width:50px;font-size:1.8em;transition:all 0.7s cubic-bezier(0.77, 0, 0.175, 1)}.XButton:hover{color:#000}.fixedPos{position:fixed}#sideNav{position:fixed;transform:translateX(-100%);top:0;left:0;bottom:0;right:0;border:0;z-index:4;white-space:nowrap;overflow-x:hidden;background-color:#000;padding-top:30px;color:#fff}#sideNav:after{content:"";position:relative;top:0;left:0;bottom:0;right:0}#sideNav .words p{font-size:1.125em;color:#fff}#sideNav .words p:nth-child(2){padding-bottom:1em}#sideNav .words p:nth-child(2):after{content:" fullstack developer."}#sideNav .words h1{text-align:center;font-size:2.25em}#sideNav .aboutIcons{position:absolute;bottom:5%;left:50%;transform:translate(-50%, -50%)}#sideNav .fa{color:#fff;padding:0 0.4em 0 0.4em;opacity:0.5;cursor:pointer}#sideNav .fa:hover{opacity:1}#sideNav .fa-envelope{position:relative;z-index:1}#sideNav .fa-envelope p{color:#fff;font-family:'D-alternate';position:absolute;transform:translateY(-50%);content:"";opacity:0;visibility:none;font-size:0.3em;left:-115%;bottom:-115%}#sideNav .fa-envelope:hover p{opacity:1;visibility:visible;cursor:default}
