.carousel_history{position:absolute;z-index:2;bottom:10px;width:50%;left:50%;transform:translate(-50%, 0%);transition-duration:0.7s}.carousel_history ul{list-style:none;padding:0;margin:0;display:flex;justify-content:center;height:2.5em}.carousel_history ul li{display:flex;justify-content:center;align-items:center;height:100%;width:1em;box-sizing:border-box;height:100%;position:relative}.carousel_history ul li:not(:last-of-type){margin-right:1em}.carousel_history ul li button{border:none;outline:none;position:absolute;cursor:pointer;background-color:rgba(255,255,255,0.5);width:100%;height:60%;border-radius:50px}.carousel_history ul li button.active{background-color:rgba(255,255,255,0.95);height:100%}.carousel_control{position:absolute;z-index:2;height:20%;top:43%;transform:translateY(-50%);background-color:rgba(0,0,0,0);border:0;outline:0;width:10%;overflow:hidden;cursor:pointer}.carousel_control span{display:inline-block;width:2vw;height:2vw;position:relative;box-sizing:border-box;opacity:0.65;border-bottom:0.5vw solid white;border-left:0.5vw solid white}.carousel_control:hover{color:rgba(0,0,0,0.2)}.carousel_control:hover span{opacity:1}.carousel_control__prev{left:0;padding-left:12px;transition-duration:0.7s}.carousel_control__prev span{transform:rotate(45deg)}.carousel_control__next{right:0;padding-right:12px;transition-duration:0.7s}.carousel_control__next span{transform:rotate(-135deg)}
